.btn {
  padding: 1em 1.5em;
  color: white;
  border: none;
  font-weight: 800;
  border-radius: 10px;
  margin: 5px; }
  .btn:hover {
    cursor: pointer; }
  .btn-primary {
    background-color: blueviolet; }
    .btn-primary:hover {
      background-color: mediumslateblue; }
  .btn-danger {
    background-color: #d50000; }
    .btn-danger:hover {
      background-color: #ff5131; }
  .btn-secondary {
    background-color: #388e3c; }
    .btn-secondary-hover {
      background-color: #6abf69; }
